.add-product-popup {
  font-size: 12px;
  .add-product-title {
    display: flex;
    min-width: 575px !important;
    align-items: center;
    justify-content: space-between;
    padding-left: 20px;
    color: #3d2277;
    font-weight: 500;
    background-color: #d7dff4;
  }
  .add-product-footer {
    padding: 20px;
  }
  .extra-spec-container {
    padding: 5px 0px;
    .label {
      font-weight: 600;
      color: #3d2277;
      padding-bottom: 5px;
    }
    .extra-spec-input-container {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      flex-basis: 142px;
      div {
        width: 50%;
        display: flex;
      }
    }
  }
  .add-item-container {
    min-width: 500px;
    margin-top: 5px;
    padding: 0px 20px;
    .field-container {
      padding: 5px 0px;
      .label {
        font-weight: 600;
        color: #3d2277;
        padding-bottom: 5px;
      }
      .custom-dropdown {
        margin-bottom: 0px;
      }
      .dimension-input-container {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        .input-field-container {
          width: 100%;
          // padding-right: 10px;
          .input-field {
            padding: 10px 10px;
            width: 86%;
            // margin-right: 10px;
          }
          .input-field:last-child {
            margin-right: 0px;
          }
        }
      }
    }
    .quantity-price-container {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      .field-container {
        width: 100%;
        margin-right: 10px;
        .custom-dropdown {
          width: 100%;
          height: 40px;
        }
        .input-field {
          padding: 0px 10px;
          width: 90%;
          height: 38px;
        }
      }
      .field-container:last-child {
        margin-right: 0px;
      }
    }
  }
  .button {
    padding: 5px;
    width: 100px;
    // background-color: #2e3192;
    font-size: 12px !important;
    div {
      display: flex;
      align-items: center;
      justify-content: center;
      svg {
        font-size: 18px;
      }
    }
  }
  .button:hover {
    background-color: #fafafa;
    color: #3d2277;
  }
  .input-title {
    width: 100%;
  }
  .model {
    display: flex;
  }
  .qty-price-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .box-item {
      width: 200px;
      .price-field {
      }
    }
  }
}
